import { allLine } from '../../../../actions'
import React, { useEffect,useState } from 'react'
import './total.scss'



export default function Total() {
  const [data, setData] = useState()
  useEffect(() => {
    allLine().then(d => setData(d))
  }, [])
  return (
    <div className='totaldata'>
      <div className='title'> :</div>
      <div className="line"><span> :</span> {data?.line}</div>
      <div className="line"><span> :</span> {data?.product}</div>
      <div className="line"><span></span> {data?.price.toLocaleString()}</div>
      <div className="line"><span>:</span> {data?.discount.toLocaleString()}</div>
      <div className="line"><span> :</span> {data?.total.toLocaleString()}</div>
    </div>
  )
}
