const initialState = {
  hits: [],
  news: [],
};

export const statusReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case " ":
        return {...state, hits: [...state.hits, ...payload]}
    case "":
        return {...state, news: [...state.news, ...payload]}
    default:
       return state
  }
};
